
@import url('https://fonts.googleapis.com/css2?family=Parkinsans:ital,wght@0,400;0,700;1,400;1,700&family=Roboto&display=swap');
:root {
    --background-color: #203162;
    --dark-text-color: #203162;
    --text-color:#abd7ff;
    --color1: #ff9d42;
    --color2: #19a28d;
    --color3: #ffc610;
    --color4: #f8f6f1;
  }
  body{
    margin: 0px;
    padding: 0px;
    color: var(--text-color);
    background-color: var(--background-color);
    font-family: Roboto, Arial, Helvetica, sans-serif;
  }
  *{
    margin: 0px;
    padding: 0px;
  }
.app-content{
    padding: 4rem;
    margin: 0;
}
h1,h2,h3,h4,h5,h6{
    font-family: Parkinsans, Arial, Helvetica, sans-serif;
}
.center-text{
    text-align: center;
}
a{
    text-decoration: none;
    text-decoration-color: none;
}
.card{
    min-height: 200px;
    min-width: 200px;
    border-radius: 1rem;
    flex:1;
}
.linked_text{
    font-weight: 600;
    /* font-size: 1.5rem; */
    padding: 1rem 2rem;
    border-radius: 2rem;
}
.linked_text a{
    color: var(--dark-text-color);
}
.colored:nth-child(4n){
    background-color: var(--color1);
}
.colored:nth-child(4n+1){
    background-color: var(--color2);
}
.colored:nth-child(4n+2){
    background-color: var(--color3);
}
.colored:nth-child(4n+3){
    background-color: var(--color4);
}
.flexbox{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    gap: 2rem;
}

.home{
    min-height: calc(100vh - 8rem);  
    flex-direction: column; 
    display: flex;
    justify-content: center;
    align-items: stretch;
}
.title{
    font-size: clamp(1.5rem, 10vw, 3rem);
    margin-bottom: clamp(10px, 10vw, 2rem);
}
.description{
    font-size: clamp(1.5rem, 10vw, 1.5rem);
    margin-bottom: clamp(10px, 10vw, 2rem);
}
.home h1{
    font-size: 4rem;
}
@media (max-width:480px) {
    .app-content{
        padding: 10px;
    }
    .home{
        min-height: calc(100vh - 20px);   
    }
}